@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap);
html{background-color:#000;font-family:"IBM Plex Mono",monospace;color:#fff}#root{display:flex;justify-content:center}.app{width:500px;margin:20px}.app h1{margin:30px 0 10px;font-size:26px}.app h2{margin-top:50px;margin-bottom:10px}.app .contract-name{cursor:pointer}.app .contract-name:hover{color:#555}.app .token-image{width:100px;height:100px;background-size:contain;background-position:center;background-repeat:no-repeat;flex-shrink:0}.app .token-image.token-image-show{text-align:center;font-size:30px;line-height:50px;cursor:pointer}.app .token-image.token-image-show:hover{color:#555}button{border:1px solid #fff;padding:5px}
